$primary : #abb400;




$mobile : 768px;

@mixin mobile {
    @media (max-width : #{$mobile}){
        @content;
    }
}

.navbar-custom{
    background: linear-gradient( to right ,white, $primary, $primary, $primary, $primary, $primary, $primary, $primary, $primary, $primary, $primary, $primary, $primary, $primary, $primary, $primary, $primary, $primary, $primary, $primary, $primary, $primary, $primary, $primary, $primary , white);
    
    padding : 0px 100px;
    
    @include mobile{
        padding : 0px 10px;
        background: $primary;
    }
}
